import { Component, OnInit, HostListener } from '@angular/core';

export enum KEY_CODE {
  RIGHT_ARROW = 39,
  LEFT_ARROW = 37,
  SPACE_BAR = 32,
  Y_KEY =  89,
  N_KEY = 78
}

@Component({
  selector: 'app-colorblind',
  templateUrl: './colorblind.component.html',
  styleUrls: ['./colorblind.component.css']
})
export class ColorblindComponent implements OnInit {

  plate_number: number = 1;
  result: number = 12;
  case_val: number = 1;
  end: number = 0;
  NotColorBlind: number = 0;
  displayResult: string = "";

  constructor() { }

  @HostListener('window:keydown', ['$event'])
  keyEvent(event: KeyboardEvent){
    // console.log(this.counter);
    console.log('show:', event.keyCode);
    if(event.keyCode === KEY_CODE.Y_KEY){
      this.case_val++;
    } else if(event.keyCode === KEY_CODE.N_KEY){
      this.end = 1;
    }

    switch (this.case_val){
      case 1: this.result = 12;
        this.plate_number = 1;
        this.displayResult = "Everyone should see number 12";
        break;
      case 2: this.result = 8;
        this.plate_number = 2;
        this.displayResult = "Red-green deficiency: 3";
        break;
      case 3: this.result = 29;
        this.plate_number = 3;
        this.displayResult = "Red-green deficiency:70";
        break;
      case 4: this.result = 5;
        this.plate_number = 4;
        this.displayResult = "Red-green deficiency: 2";
        break;
      case 5: this.result = 3;
        this.plate_number = 5;
        this.displayResult = "Red-green deficiency: 5";
        break;
      case 6: this.result = 15;
        this.plate_number = 6;
        this.displayResult = "Red-green deficiency: 17";
        break;
      case 7: this.result = 74;
        this.plate_number = 7;
        this.displayResult = "Red-green deficiency: 21";
        break;
      case 8: this.result = 6;
        this.plate_number = 8;
        this.displayResult = "Red-green deficiency: most people don’t see anything or see something wrong.";

        break;
      case 9: this.result = 45;
        this.plate_number = 9;
        this.displayResult = "Red-green deficiency: most people don’t see anything or see something wrong.";
        break;
      case 10: this.result = 5;
        this.plate_number = 10;
        this.displayResult = "Red-green deficiency: 5";
        break;
      case 11: this.result = 7;
        this.plate_number = 11;
        this.displayResult = "Red-green deficiency: most people don’t see anything or see something wrong.";
        break;
      case 12: this.result = 16;
        this.plate_number = 12;
        this.displayResult = "Red-green deficiency: most people don’t see anything or see something wrong.";
        break;
      case 13: this.result = 73;
        this.plate_number = 13;
        this.displayResult = "Red-green deficiency: most people don’t see anything or see something wrong.";
        break;
      case 14: this.NotColorBlind = 1;
        break;
      default:
        break;
    }
  }

  ngOnInit() {}

}
