import { Component, OnInit, HostListener } from '@angular/core';

export enum KEY_CODE {
  RIGHT_ARROW = 39,
  LEFT_ARROW = 37,
  SPACE_BAR = 32,
  Y_KEY =  89,
  N_KEY = 78
}

@Component({
  selector: 'app-astigmatism',
  templateUrl: './astigmatism.component.html',
  styleUrls: ['./astigmatism.component.css']
})
export class AstigmatismComponent implements OnInit {

noStigmatism: boolean = false;
Stigmatism: boolean = false;

  constructor() { }
  @HostListener('window:keydown', ['$event'])
  keyEvent(event: KeyboardEvent){
    // console.log(this.counter);
    console.log('show:', event.keyCode);
    if(event.keyCode === KEY_CODE.Y_KEY){
      this.Stigmatism=true;
      this.noStigmatism = false;
    } else if(event.keyCode === KEY_CODE.N_KEY){
      this.noStigmatism = true;
      this.Stigmatism=false;
    }
  }

  ngOnInit() {
  }

}
